      text-align: center;
      font-size: 48px;
      font-weight: 700;
      color: #ffffff; }
      @media only screen and (max-width: 650px) {
        #home-wrapper #headline span {
          font-size: 32px; } }
  #home-wrapper #about,
  #home-wrapper #products,
  #home-wrapper #contact {
    min-height: 100vh;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 80px 40px; }
  #home-wrapper #about {
    background-color: rgba(255, 255, 255, 0.8); }
  #home-wrapper #products {
    background-color: #ffffff; }
  #home-wrapper #contact {
    background-color: rgba(255, 255, 255, 0.8); }



