@-webkit-keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes slide-up {
  from {
    padding-top: 20px;
    opacity: 0; }
  to {
    padding-top: 0px;
    opacity: 1; } }

@keyframes slide-up {
  from {
    padding-top: 20px;
    opacity: 0; }
  to {
    padding-top: 0px;
    opacity: 1; } }

@-webkit-keyframes spin {
  100% {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg); } }

@keyframes spin {
  100% {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg); } }

@-webkit-keyframes bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

#about-container {
  font-size: 21px;
  max-width: 1150px;
  margin: auto; }
  @media only screen and (max-width: 650px) {
    #about-container {
      font-size: 16px; } }
  #about-container #about-title {
    font-size: 48px;
    padding: 0px 40px;
    margin-top: 20px;
    float: left; }
    @media only screen and (max-width: 650px) {
      #about-container #about-title {
        font-size: 32px;
        text-align: center;
        float: none; } }
  #about-container #about-content {
    width: auto;
    overflow: hidden; }



